@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: "Nunito", sans-serif;
}

@font-face {
  font-family: "Rockwell";
  src: url(/static/media/Rockwell.5985ac00.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}

/* Set Color Here */
:root {
  --primary-color: #27409a;
  --second-color: #fed03f;
  --secondary-color: #506ac7;
  --secondary-light-color: #112a82;
  --third-color: #2ac3ee;
  --white-color: white;
  --light-color: #959595;
}

.linkStyle {
  color: inherit;
  text-decoration: inherit;
}

/* Button Style */
.header-button {
  background-color: #506ac7;
  background-color: var(--secondary-color);
  padding: 10px;
  color: white;
  color: var(--white-color);
  border: 0;
  border-radius: 6px;
  cursor: pointer;
}
.header-button1 {
  background-color: #fed03f;
  background-color: var(--second-color);
  padding: 15px 30px 15px 30px;
  color: #27409a;
  color: var(--primary-color);
  border: 0;
  border-radius: 6px;
  cursor: pointer;
}

.primary-button {
  background-color: #27409a;
  background-color: var(--primary-color);
  padding: 18px;
  font-size: 16px;
  color: white;
  color: var(--white-color);
  border: 0;
  border-radius: 30px;
  cursor: pointer;
  box-shadow: 0px 0px 42px -12px rgba(99, 235, 228, 1);
}

/*Form Style*/
input[type="text"] {
  width: 100%;
  padding: 12px;
  margin: 10px 10px;
  margin: 8px 0;
  box-sizing: border-box;
  border-radius: 6px;
  border: 2px solid white;
  border: 2px solid var(--white-color);
  background-color: white;
  background-color: var(--white-color);
  color: #27409a;
  color: var(--primary-color);
}

/* Reset Select */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  border-radius: 30px;
  background-image: none;
}

/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.select {
  position: relative;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 3em;
  line-height: 3;
  margin: 10px 10px 8px 0;

  background: white;

  background: var(--white-color);
  color: black;
  overflow: hidden;
  border-radius: 0.25em;
}
select {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 10px;
  cursor: pointer;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #fed03f;
  background: var(--second-color);
  cursor: pointer;
  color: #27409a;
  color: var(--primary-color);
  pointer-events: none;
  transition: 0.25s all ease;
}

/* Transition */
.select:hover::after {
  color: #506ac7;
  color: var(--secondary-color);
}

/* Font Awesome */
.fa-chevron-down {
  font-size: 10px;
  padding-left: 10px;
}

.fa-facebook-f {
  color: white;
  color: var(--white-color);
  margin-bottom: 10px;
}

.fa-instagram {
  color: white;
  color: var(--white-color);
  margin-bottom: 10px;
}

.fa-linkedin {
  color: white;
