  height: 0;
}
.video video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  min-height: 0;
  border-radius: 12px;
  border: 3px solid white;
  box-shadow: 0px 0px 42px -12px rgba(99, 235, 228, 1);
}

.video-gallery {
  margin-top: 3em;
