  width: 100%;
}
.video-gallery .video {
  -webkit-animation: 1200ms fadeOut ease;
          animation: 1200ms fadeOut ease;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  grid-column-start: 1;
  grid-row-start: 1;
  grid-row-end: 7;
  max-height: 19.5em;
  opacity: 0;
  transition: all 300ms ease;
}
